export default {
	props: {
		shixian : Boolean
	},
	data() {
		const date = new Date();
		const years = [];
		const year = date.getFullYear()+40
		const months = []
		let month = date.getMonth() + 1
		const days = []
		let day = date.getDate()
		for (let i = 1940; i <= date.getFullYear()+80; i++) {
			years.push(i)
		}
		for (let i = 1; i <= 12; i++) {
			months.push(i)
		}
		for (let i = 1; i <= 31; i++) {
			// for(let j =0;j<years.length;j++){
			// 	if(years[j]%4==0 % 100 != 0 || n % 400 == 0)
			// }
			days.push(i)
		}
		return {
			title: 'picker-view',
			years,
			year,
			months,
			month,
			days,
			day,
			values: [],
			visible: true,
			indicatorStyle: `height: ${Math.round(uni.getSystemInfoSync().screenWidth/(750/100))}px;`
		};
	},
	methods: {	
		confirm(value){
			console.log(value);
			var dates = value.split("-");
			this.values[0] = dates[0] - 1940;
			this.values[1] = dates[1] - 1;
			this.values[2] = dates[2] - 1;
			this.year = dates[0];
			this.month = dates[1];
			this.day = dates[2];
			console.log(this.month);
		}, 
		bindChange: function (e) {
			const val = e.detail.value;
			// console.log(val[0]+1940);
			// console.log(val[1]+1);
			// console.log(val[2]+1);
			const v0=val[0]+1940;
			const v1=val[1]+1;
			if(v1==2){
				if(v0% 4 == 0 && v0 % 100 != 0 || v0 % 400 == 0){
					this.days.splice(28,3)
				}else{
					this.days.splice(29,2)
				}
				
			}else if(v1==4||v1==6||v1==9||v1==11){
				this.days.splice(30,1)
			
			}else{
				if(this.days.length==28){
					this.days.splice(28,0,"29","30","31")
				}else if(this.days.length==29){
					this.days.splice(29,0,"30","31")
				}else if(this.days.length==30){
					this.days.splice(30,0,"31")
				}else{
					
				}
				
			}
			if( val.length > 0 ){
				this.year = this.years[val[0]]
				// this.month = this.months[val[1]] > 9 ? this.month : '0' + this.months[val[1]];
				
					this.month = this.months[val[1]];
			
					
				
				// this.day = this.days[val[2]] > 9 ? this.month : '0' + this.days[val[2]];
				if (this.days[val[2]] > 9) {
					this.day = this.days[val[2]];
				}else{
					this.day = '0' + this.days[val[2]];
				}
			}				
		},
		quxiaobutton () {
			this.values = [9999, this.month - 1, this.day - 1];
			this.$emit('quxiaoButton');
		},
		quedingbutton () {

			this.values = [9999, this.month - 1, this.day - 1];
			console.log(this.year,this.month,this.day)
			this.$emit('quedingButton',this.year,this.month,this.day);	
		}
	}
	
}
